'use client'

import { useState, useEffect, useRef } from 'react'
import { useRouter, useParams } from 'next/navigation'
import ChatSidebar from '@/components/ChatSidebar'
import ChatHeader from '@/components/ChatHeader'
import ChatMessage from '@/components/ChatMessage'
import ChatInput from '@/components/ChatInput'
import { ScrollArea } from '@/components/ui/scroll-area'

interface Message {
  id: string
  text: string
  sender: 'self' | 'other'
  timestamp: string
  senderName?: string
}

interface Contact {
  id: string
  name: string
}

export default function ChatRoom() {
  const params = useParams()
  const router = useRouter()
  const chatId = params.id as string
  const [user, setUser] = useState<{ email: string; name: string } | null>(null)
  const [contact, setContact] = useState<Contact | null>(null)
  const [messages, setMessages] = useState<Message[]>([])
  const [isLoading, setIsLoading] = useState(true)
  const messagesEndRef = useRef<HTMLDivElement>(null)

  // Mock contacts data
  const contacts: Record<string, Contact> = {
    '1': { id: '1', name: 'Alice Johnson' },
    '2': { id: '2', name: 'Bob Smith' },
    '3': { id: '3', name: 'Carol Davis' },
    '4': { id: '4', name: 'David Wilson' },
    '5': { id: '5', name: 'Emma Brown' },
    '6': { id: '6', name: 'Frank Miller' },
    '7': { id: '7', name: 'Grace Lee' }
  }

  // Mock messages data
  const mockMessages: Record<string, Message[]> = {
    '1': [
      { id: '1', text: 'Hey! How are you doing?', sender: 'other', timestamp: '2:25 PM', senderName: 'Alice Johnson' },
      { id: '2', text: 'I\'m doing great, thanks! How about you?', sender: 'self', timestamp: '2:26 PM' },
      { id: '3', text: 'Pretty good! Just working on some projects', sender: 'other', timestamp: '2:27 PM', senderName: 'Alice Johnson' },
      { id: '4', text: 'That sounds interesting! What kind of projects?', sender: 'self', timestamp: '2:28 PM' },
      { id: '5', text: 'Mostly web development stuff. Building some cool apps!', sender: 'other', timestamp: '2:30 PM', senderName: 'Alice Johnson' }
    ],
    '2': [
      { id: '1', text: 'Thanks for the help yesterday', sender: 'other', timestamp: '1:10 PM', senderName: 'Bob Smith' },
      { id: '2', text: 'No problem at all! Happy to help', sender: 'self', timestamp: '1:12 PM' },
      { id: '3', text: 'The solution worked perfectly', sender: 'other', timestamp: '1:15 PM', senderName: 'Bob Smith' }
    ],
    '3': [
      { id: '1', text: 'See you at the meeting tomorrow', sender: 'other', timestamp: '12:40 PM', senderName: 'Carol Davis' },
      { id: '2', text: 'Absolutely! What time was it again?', sender: 'self', timestamp: '12:42 PM' },
      { id: '3', text: '10 AM in the conference room', sender: 'other', timestamp: '12:45 PM', senderName: 'Carol Davis' }
    ],
    '4': [
      { id: '1', text: 'The project looks great!', sender: 'other', timestamp: '11:25 AM', senderName: 'David Wilson' },
      { id: '2', text: 'Thank you! I put a lot of work into it', sender: 'self', timestamp: '11:27 AM' },
      { id: '3', text: 'It really shows. Excellent job!', sender: 'other', timestamp: '11:30 AM', senderName: 'David Wilson' }
    ],
    '5': [
      { id: '1', text: 'Can you send me the files?', sender: 'other', timestamp: 'Yesterday', senderName: 'Emma Brown' },
      { id: '2', text: 'Sure! I\'ll send them right now', sender: 'self', timestamp: 'Yesterday' }
    ],
    '6': [
      { id: '1', text: 'Perfect, let\'s do it', sender: 'other', timestamp: 'Yesterday', senderName: 'Frank Miller' },
      { id: '2', text: 'Great! I\'ll get started on it', sender: 'self', timestamp: 'Yesterday' }
    ],
    '7': [
      { id: '1', text: 'Happy birthday! üéâ', sender: 'other', timestamp: 'Monday', senderName: 'Grace Lee' },
      { id: '2', text: 'Thank you so much! üòä', sender: 'self', timestamp: 'Monday' }
    ]
  }

  useEffect(() => {
    // Check if user is logged in
    const userData = localStorage.getItem('user')
    if (!userData) {
      router.push('/login')
      return
    }
    setUser(JSON.parse(userData))

    // Load contact and messages
    const currentContact = contacts[chatId]
    if (!currentContact) {
      router.push('/chats')
      return
    }

    setContact(currentContact)
    setMessages(mockMessages[chatId] || [])
    setIsLoading(false)
  }, [chatId, router])

  useEffect(() => {
    // Scroll to bottom when messages change
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' })
  }, [messages])

  // Simulate receiving messages
  useEffect(() => {
    if (!contact || messages.length === 0) return

    const interval = setInterval(() => {
      // Randomly send a message from the contact (10% chance every 10 seconds)
      if (Math.random() < 0.1) {
        const responses = [
          'That\'s interesting!',
          'I see what you mean',
          'Sounds good to me',
          'Let me think about that',
          'Great idea!',
          'I agree with you',
          'That makes sense'
        ]
        
        const randomResponse = responses[Math.floor(Math.random() * responses.length)]
        const newMessage: Message = {
          id: Date.now().toString(),
          text: randomResponse,
          sender: 'other',
          timestamp: new Date().toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' }),
          senderName: contact.name
        }
        
        setMessages(prev => [...prev, newMessage])
      }
    }, 10000) // Check every 10 seconds

    return () => clearInterval(interval)
  }, [contact, messages.length])

  const handleSendMessage = (messageText: string) => {
    const newMessage: Message = {
      id: Date.now().toString(),
      text: messageText,
      sender: 'self',
      timestamp: new Date().toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })
    }

    setMessages(prev => [...prev, newMessage])
  }

  if (isLoading || !user || !contact) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="text-center">
          <p className="text-gray-600">Loading chat...</p>
        </div>
      </div>
    )
  }

  return (
    <div className="flex h-screen">
      <ChatSidebar selectedChatId={chatId} />
      
      {/* Chat Area */}
      <div className="flex-1 flex flex-col">
        <ChatHeader contactName={contact.name} />
        
        {/* Messages Area */}
        <ScrollArea className="flex-1 bg-gray-50">
          <div className="p-4">
            {messages.length === 0 ? (
              <div className="text-center text-gray-500 mt-8">
                <p>No messages yet. Start the conversation!</p>
              </div>
            ) : (
              messages.map((message) => (
                <ChatMessage
                  key={message.id}
                  message={message.text}
                  sender={message.sender}
                  timestamp={message.timestamp}
                  senderName={message.senderName}
                />
              ))
            )}
            <div ref={messagesEndRef} />
          </div>
        </ScrollArea>
        
        <ChatInput onSendMessage={handleSendMessage} />
      </div>
    </div>
  )
}
