'use client'

interface ChatMessageProps {
  message: string
  sender: 'self' | 'other'
  timestamp: string
  senderName?: string
}

export default function ChatMessage({ message, sender, timestamp, senderName }: ChatMessageProps) {
  const isSelf = sender === 'self'

  return (
    <div className={`flex mb-4 ${isSelf ? 'justify-end' : 'justify-start'}`}>
      <div className={`max-w-xs lg:max-w-md px-4 py-2 rounded-lg ${
        isSelf 
          ? 'bg-green-500 text-white' 
          : 'bg-white text-gray-900 border border-gray-200'
      }`}>
        {!isSelf && senderName && (
          <p className="text-xs font-medium text-gray-600 mb-1">{senderName}</p>
        )}
        <p className="text-sm">{message}</p>
        <p className={`text-xs mt-1 ${
          isSelf ? 'text-green-100' : 'text-gray-500'
        }`}>
          {timestamp}
        </p>
      </div>
    </div>
  )
}
